import 'piral/polyfills';
import { renderInstance,  Extend } from 'piral'; //to render everything
import { layout, errors } from './layout';

//api key: ac36fb1ce817971c07fec3eb8d5bdcf7c8d1606d7d8dd87e8c080504f2187c28 - tutorials
// change to your feed URL here (either using feed.piral.cloud or your own service)
const feedUrl = 'https://feed.piral.cloud/api/v1/pilet/tut0rials';

declare module 'piral-core/lib/types/custom' {
  interface PiletCustomApi extends MyPiletApi {}
}

interface MyPiletApi {
  foo(): string;
};

const createMyApi = (): Extend<MyPiletApi> => {
  return () => ({
    foo() {
      return "Hello!";
    }
  });
};

const { root } = renderInstance({
  layout,
  errors,
  extendApi: [createMyApi()],
  requestPilets() {
    return fetch(feedUrl)
      .then(res => res.json())
      .then(res => res.items);
  },
});

setTimeout(() => {
  alert(root.foo());
}, 2000);
